/* Page Indicator */

.indicator-container {
  max-width: 500px;
}

.ProgressBar {
  margin: 0 auto;
  padding: 2.5em 0 2.5em;
  list-style: none;
  position: relative;
  display: flex;
  justify-content: space-between;
}
.ProgressBar-step {
  text-align: center;
  position: relative;
  width: 100%;
}
.ProgressBar-step:before,
.ProgressBar-step:after {
  content: "";
  height: 0.35em;
  background-color: #594fd1;
  opacity: 0.2;
  position: absolute;
  z-index: 1;
  width: 100%;
  left: -50%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: all 0.25s ease-out;
  transition: all 0.25s ease-out;
}
.ProgressBar-step:first-child:before,
.ProgressBar-step:first-child:after {
  display: none;
}
.ProgressBar-step:after {
  background-color: #594fd1;
  width: 0%;
}
.ProgressBar-step.is-complete + .ProgressBar-step.is-current:after,
.ProgressBar-step.is-complete + .ProgressBar-step.is-complete:after {
  opacity: 0.5;
  width: 100%;
}
.ProgressBar-icon {
  width: 0.65em;
  height: 0.65em;
  background-color: #f6f5fd;
  fill: #f6f5fd;
  border-radius: 50%;
  border-style: solid;
  border-width: 1px;
  border-color: #594fd1;
  padding: 0.3em;
  max-width: 100%;
  z-index: 10;
  position: relative;
  -webkit-transition: all 0.25s ease-out;
  transition: all 0.25s ease-out;
}
.is-current .ProgressBar-icon {
  fill: #594fd1;
  background-color: #594fd1;
}
.is-complete .ProgressBar-icon {
  fill: #f6f5fd;
  background-color: #594fd1;
}


